import React from 'react';
import { Link } from 'react-router-dom';
import { mapCharacters } from '../characterMapping';

const FeaturedProductCard = ({ product, isLoading, priority }) => {
  if (isLoading) {
    return (
      <div className="border p-4 rounded-lg shadow-lg animate-pulse">
        <div className="w-full h-64 bg-gray-300 rounded-md"></div>
        <div className="mt-4 h-6 bg-gray-300 rounded w-3/4"></div>
        <div className="mt-2 h-4 bg-gray-300 rounded w-1/2"></div>
      </div>
    );
  }

  if (!product) {
    return null;
  }

  const {
    ref_number,
    brand,
    price,
    discounted_price,
    image_urls,
    outOfStock
  } = product;

  const mappedReference = mapCharacters(ref_number || '');
  const productLink = `/product/${mappedReference}`;

  const imageUrl = product.image_urls?.thumbnail || product.image_urls?.medium || product.image_urls?.main || '/path/to/default-image.jpg';

  const formatPrice = (p) => p ? `${Number(p).toLocaleString()} IQD` : "No Price";

  return (
    <Link to={productLink} className="group block bg-white rounded-lg overflow-hidden hover:shadow-2xl transform hover:-translate-y-2 transition-all duration-300 ease-in-out h-full flex flex-col">
      <div className="relative flex-grow flex items-center justify-center p-4 bg-white-50">
        <img
          src={imageUrl}
          alt={`${brand} - ${ref_number}`}
          className="w-full h-64 object-contain transition-transform duration-300 group-hover:scale-110"
          loading={priority ? 'eager' : 'lazy'}
        />
        {outOfStock && (
          <div className="absolute inset-0 bg-black bg-opacity-60 flex items-center justify-center">
            <span className="text-white font-bold text-base bg-red-700 px-3 py-1 rounded-full">OUT OF STOCK</span>
          </div>
        )}
      </div>
      <div className="p-6 text-center">
        <h3 className="text-sm font-semibold text-gray-500 uppercase tracking-wider">{brand || 'No Brand'}</h3>
        <p className="text-lg font-medium text-gray-800 mt-1 truncate">{ref_number || 'No Refs'}</p>
        <div className="mt-4 text-xl font-bold text-gray-900">
          {discounted_price > 0 && discounted_price < price ? (
            <div className="flex items-center justify-center space-x-2">
              <span className="text-red-600">{formatPrice(discounted_price)}</span>
              <span className="text-gray-500 line-through text-base">{formatPrice(price)}</span>
            </div>
          ) : (
            <span>{formatPrice(price)}</span>
          )}
        </div>
      </div>
    </Link>
  );
};

export default FeaturedProductCard;