import React from 'react';
import { Link } from 'react-router-dom';
import { mapCharacters } from '../characterMapping';

const ProductCard = ({ product, isLoading, priority }) => {
  if (isLoading) {
    return (
      <div className="border p-4 rounded-lg shadow-lg animate-pulse">
        <div className="w-full h-48 bg-gray-300 rounded"></div>
        <div className="mt-4 h-6 bg-gray-300 rounded w-3/4"></div>
        <div className="mt-2 h-4 bg-gray-300 rounded w-1/2"></div>
      </div>
    );
  }

  if (!product) {
    return null;
  }

  const {
    ref_number,
    brand,
    price,
    discounted_price,
    image_urls,
    outOfStock
  } = product;

  const mappedReference = mapCharacters(ref_number || '');
  const productLink = `/product/${mappedReference}`;

  // --- FIX: Prioritize 'thumbnail' and handle missing image_urls object ---
  const imageUrl = product.image_urls?.thumbnail || product.image_urls?.medium || product.image_urls?.main || '/path/to/default-image.jpg';

  const formatPrice = (p) => p ? `${Number(p).toLocaleString()} IQD` : "No Price";

  return (
    <Link to={productLink} className="group block border rounded-lg overflow-hidden shadow-sm hover:shadow-xl transition-shadow duration-300 h-full flex flex-col">
      <div className="relative flex-grow flex items-center justify-center bg-white">
        <img
          src={imageUrl}
          alt={`${brand} - ${ref_number}`}
          // --- FIX: Changed to object-contain ---
          className="w-full h-48 object-contain transition-transform duration-300 group-hover:scale-105 p-2"
          loading={priority ? 'eager' : 'lazy'}
        />
        {outOfStock && (
          <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center">
            <span className="text-white font-bold text-sm bg-red-600 px-2 py-1 rounded">OUT OF STOCK</span>
          </div>
        )}
      </div>
      <div className="p-4 bg-gray-50">
        <h3 className="text-lg font-semibold truncate">{brand || 'No Brand'}</h3>
        <p className="text-sm text-gray-600">{ref_number || 'No Refs'}</p>
        <div className="mt-2 text-lg font-bold">
          {discounted_price > 0 && discounted_price < price ? (
            <div>
              <span className="text-red-500 line-through text-sm">{formatPrice(price)}</span>{' '}
              <span>{formatPrice(discounted_price)}</span>
            </div>
          ) : (
            <span>{formatPrice(price)}</span>
          )}
        </div>
      </div>
    </Link>
  );
};

export default ProductCard;